// components
import Nav from "Shared/ts/components/nav";
import Accordion from "Shared/ts/components/accordion"; // utils

import LoadItem from "Shared/ts/utils/load-item";
import { elementExists } from "shared/ts/utils/html"; // observers

import { observer } from "shared/ts/observers/intersection";
var nav = new Nav("nav");
observer("[data-src-img]", {
  inRange: function inRange(record) {
    return new LoadItem(record);
  }
});
observer("[data-src-iframe]", {
  inRange: function inRange(record) {
    return new LoadItem(record, {
      tag: "iframe",
      src: "data-src-iframe"
    });
  }
});
var accordion = document.querySelector(".accordion");

if (elementExists(accordion)) {
  new Accordion(accordion);
}